@import './config';
@import './services';
@import './header';
@import './navbar';
@import './shop';
@import './location';
@import './servicetwo';
@import './footer';
@import './card';

section{
    overflow: hidden;
    scroll-behavior: unset;
}

html{
    scroll-behavior: smooth;
}

a:hover, svg:hover{
    color: $greenDark;
    transition: all 0.2s;
    cursor: pointer;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Hind Siliguri', sans-serif;
}

li{
    list-style-type: none;
}

a, h1, h2, h3{
    text-decoration: none;
  
}

small{
    color: $white;
}

