// Mobile Styles
.servicesContainer{
    height: 1209px;
    // height: 170vh;
    // height: 100vh;
    @include mobileViewContainer;
    justify-items: center;

    img{
        height:195px; 
        width: 275px;
    }

    .cardContainer .text p:nth-child(2) {
        padding-top: 118px;
    }

}

.text-container{
    grid-column: 2 / 8;
    grid-row: 1 ;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    text-align: center;
    margin-top: 40px;

    .text1{
        // grid-column: 3 / 8;
        // grid-row: 1 / 3;

        font-size: 1.2rem;
        color: black;
        font-weight: map-get($font-weights, normal);
    }

    .text2{
        // grid-column: 3 / 8;
        // grid-row: 6;

        margin-top: 25px;
        font-size: 2.2rem;
        color: #386e38;
        font-weight: map-get($font-weights, bold);
    }
}

.cards-container{
    grid-column: 2 / 8;
    grid-row: 3;
    position: relative;
 
    display: flex;
    flex-direction: column;
    align-items: center;
   
    & > *{
        margin: 10px 0;
    }
}

.serviceButton{
    grid-column: 3 / 7;
    grid-row: 7;
    @include buttonStyle;
}



// Tablet Styles
@media screen and (min-width: 620px){
.servicesContainer{
    height: 130vh;
    width: 100%;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(12, 1fr);
    justify-items: center;

                  
    .serviceButton{
        grid-column: 5 / 7;
        grid-row: 10;
        margin-top: 30px;
    }

}
  
    .text-container{
        grid-column: 3 / 9;
        grid-row: 1 / 4;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        text-align: center;
        margin-top: 40px;

        .text1{
            grid-column: 3 / 8;
            grid-row: 1 / 3;
        }

        .text2{
            grid-column: 3 / 8;
            grid-row: 5;
        }
    }

    .cards-container{
        grid-column: 1 / 13;
        grid-row: 9;

        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-evenly;

        img{
            height:170px; 
            width: 255px;
        }
    }  

         
    .cardContainer .text{
        p:nth-child(2){
            padding-top: 100px;
        }
    }

    }

   



// Desktop Styles
@media screen and (min-width: 1200px){
    .servicesContainer{
        height: 100vh;
        width: 100%;
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: repeat(12, 1fr);
        text-align: center;

                       
        .serviceButton{
            grid-column: 6 / 8;
        
        }
    }
        .test{
            grid-column: 6 / 8;
            grid-row: 10;
            margin-top: 30px;
        }

        .text-container{
            grid-column: 4 / 10;
            grid-row: 1 / 4;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: column;
            text-align: center;
            margin-top: 40px;

            .text1{
                grid-column: 3 / 8;
                grid-row: 1 / 3;
            }

            .text2{
                grid-column: 3 / 8;
                grid-row: 5;
            }

            
        }

        .cards-container{
            grid-column: 1 / 13;
            grid-row: 8;
            text-align: start;
            display: flex;
            flex-direction: row;
            align-items: center;
            flex-wrap: wrap;
            
        }

        .cards-container > * {
            margin: 15px;
        }


        .cards-container img{
            height:170px; 
            width: 255px;
        }
        
        

        .gardener{
            grid-column: 2;
            grid-row: 10;

            
            width: 200px;
            flex-direction: column;
            align-items: flex-start;

            p{
                font-size: 1rem;
                opacity: 70%;
                text-align: start;
            }
        }
}  

