// Card Style
.cardContainer{
    position: relative;
    transition: transform .2s;     
}

.cardContainer .text{
    z-index: 1000;
    position: absolute;
    top: 0;
    padding-left: 20px;
    font-size: 1.4rem;
    font-weight: map-get($font-weights, bold);
    color: white;
        
    p:first-child{
        padding-top: 10px;
    }
    p:nth-child(2){
        padding-top: 115px;
    }
}


@media screen and (min-width: 1200px){
    .cardContainer{
        &:hover{
            transform: scale(1.1);
        }
    }    
}
