// Mobile Styles
.shopContainer{
    height: 152vh;
    // height: 135vh;
    // height: 100vh;
    @include mobileViewContainer;
    background-size: cover;
    position: relative;

    .firstPart p:first-of-type, .green{
        color: $green;
    }

    p{
        color: white;
    }

    .locationDecoration{
        width: inherit;
        bottom: 0;
        position: absolute;
        filter: brightness(70%);
    }
}
.firstPart{
    grid-column: 2 / 8;
    grid-row: 2;
    text-align: center;

    h3{
        font-size: 2.5rem;
        color: white;
        text-shadow:  0px 7px 20px black;
        line-height: 1;
        padding-bottom: 20px;
    }

    p{
        font-size: 1.2rem;
        color: white;
        text-shadow:  0px 7px 10px black;
    }

    p:first-of-type{
        margin-bottom: 10px;
        text-transform: uppercase;
    }
}


.secondPart{
    grid-column: 2 / 8;
    grid-row: 6 / 8;


    display: flex;
    flex-direction: column;
    align-items: center;

    & > *{
        margin: 10px 0;
    }

    img{
        width: 250px;
    }
}

.shopButton{
    grid-column: 3 / 7;
    grid-row: 4;
    margin-top: 30px;
    @include buttonStyle;
}


.gardener{
    grid-column: 3 / 7;
    grid-row: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    z-index: 999;
    
    p{
        font-size: 1.5rem;
        text-align: center;
        width: 100%;
        font-weight: map-get($font-weights, bold);
    }

    img{
        width: 70px;
        height: 70px;
    }
}

// Tablet Styles
@media screen and (min-width: 620px){
    .shopContainer{
        grid-template-columns: repeat(10, 1fr);
        grid-template-rows: repeat(12, 1fr);
        height: 100vh;
    }
    .firstPart{
        grid-column: 3 / 9;
        grid-row: 2 / 3;
    }

    .shopButton{
        grid-column: 5 / 7;
        grid-row: 6;
        @include buttonStyle;
        margin-top: 12px;
    }
    
    .gardener{
        flex-direction: row;
        grid-column: 5 / 7;
        grid-row: 8;
        margin-bottom: 20px;
        margin-top: 0;
    }

    .secondPart{
        grid-column: 3 / 9;
        grid-row: 9 /10;
        flex-direction: row;
        justify-content: space-around;
        & > a{
            margin: 0 20px;
        }
    }
}

// Desktop Styles
@media screen and (min-width: 1200px){
    .shopContainer{
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: repeat(12, 1fr);
    }
    .firstPart{
        grid-column: 2 / 7;
        grid-row: 3 / 7;
        text-align: start;      

        h3{
            font-size: 4.5rem;
            font-weight: 600;
        }
    }

    .shopButton{
        grid-column: 2 / 7;
        grid-row: 8;
        z-index: 1000;
        margin-bottom: 60px;
    }

    .gardener{
        grid-column: 2;
        grid-row: 9;      
        width: 200px;
        flex-direction: column;
        align-items: flex-start;

        p{
         
            font-size: 1rem;
            opacity: 70%;
            text-align: start;
        }
    } 

    .grapes {
        transition: transform .2s ease-in-out;
    
        &:hover {
            transform: rotate(25deg);
        }
    }
    
    .secondPart{
        grid-column: 7 / 12;
        grid-row: 7;
    }
}