// Mobile Styles
.serviceTwoContainer{
    @include mobileViewContainer;
    height: 100vh;
    position: relative;
    align-items: center;

    .serviceTwoButton{
        grid-column: 3 / 7;
        grid-row: 5;
        @include buttonStyle;
        z-index: 8888;
    }
}

.greenText{
    color: $green;
}

.locationDecoration{
    width: inherit;
    bottom: 0;
    position: absolute;
}

.mask{
    position: absolute;
    grid-column: 2 / 8;
    grid-row: 2 / 8;
    width: 100%;
    height: 100%; 
    z-index: 101;
}

.textContainer{
    grid-column: 3 / 7;
    grid-row: 3 ;
    height: 315px;
    width: inherit;
    z-index: 103;
    display: flex;
    text-align: center;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: space-around;
    justify-content: center;

    h3{
        color: white;
        font-size: 2.2rem;
        line-height: 1;
        word-spacing: 3px;
        margin-top: 15px;
    }

    .text{
        width: 130%;
        color: white;
        font-size: 1.3rem;
        line-height: 1;
    }
}



.bottom{
    grid-column: 2 / 8;
    grid-row: 2 / 8;
    width: 100%;
    height: 100%; 
    z-index: 100;
    position: relative;
}


// Tablet Styles
@media screen and (min-width: 620px){
    .serviceTwoContainer{
       @include tabletViewContainer;
    
            
        .mask{
            position: absolute;
            grid-column: 2 / 10; 
            z-index: 101;
        }

        .bottom{
            grid-column: 2/ 10;
            width: 100%;
            height: 100%; 
            z-index: 100;
            position: relative;
        }

        .textContainer{
            grid-column: 1 / 10;
            grid-row: 3/8;
            margin-left: 32px;
            flex-direction: column;
            align-content: flex-start;
            justify-content: flex-start;
            align-items: center;
            text-align: center;
            flex-wrap: nowrap;

            h3{
                font-size: 3.6rem;
                line-height: 1;
                word-spacing: 3px;
                color: white;
            }
        }

        .serviceTwoButton{
            margin-top: 30px;
            grid-column: 5 / 7;
            grid-row: 7;
        }

        
        .text{
            margin-top: 30px;
            font-size: 1.2rem;
            line-height: 1;
            width: 80%;
        }

    }
}


// Desktop Styles
@media screen and (min-width: 1200px){
    .serviceTwoContainer{
        @include desktopViewContainer;

        .bottom{
            grid-column: 3/ 11;
        }      

        .mask{
            grid-column: 3/ 11;
        }

        .textContainer{
            grid-column: 3 / 10;
            text-align: start;
            align-items: flex-start;
            margin-left: 35px;
          

            h3{
                font-size: 4.5rem;
                line-height: 1;
                word-spacing: 3px;
                color: white;
                margin-right: 45px;
                margin-top: 0;
            }

            text{
                margin-top: 30px;
                font-size: 1.2rem;
                line-height: 1;
                width: 0;
                opacity: 80%;
            }

            
        }

        .serviceTwoButton{
            grid-column: 3 / 10;
            margin-top: 0px;
            margin-left: 35px;
        }
    }
}