// Mobile Styles
.locationContainer{
    @include mobileViewContainer;
    height: 100vh;
    position: relative;
}
.decoration{
    filter: brightness(70%);
    width: 100%;
    bottom: 0;
    position: absolute;
}

.leafDesign{
    position: absolute;
    z-index: 9;
    width: 100%;
    height: 100%;
}

.blurredDiv{
    grid-column: 2 / 8;
    grid-row: 3 / 11;  
    background: rgba(33, 63, 54, 1%);
    z-index: 2;
    border-radius: 5%;
    backdrop-filter: blur(20px);
    box-shadow: 8px 8px 12px #1c271e;
    width: 100%;
}

.locationContainer .content{
    grid-column: 2 / 8;
    grid-row: 3 / 11;
    text-align: center;
    color: $white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 11;
    background: rgba(33, 63, 54, 80%);
    
    color: white;
    border-radius: 5%;
    box-shadow: 8px 5px 12px #1c271e;

    & img{
        width: 50px;
    }

    h3{
        font-size: 2rem;
        font-weight: map-get($font-weights, 'bold');
        padding: 5px 0;
    }

    p{
        font-size: 1.2rem;
        width: 90%;
    }
    p:first-of-type{
        padding-top: 15px;
        font-weight: map-get($font-weights, 'normal');
    }

    p:nth-of-type(2){
        padding-top: 15px;
        padding-bottom: 30px;
    }

    .locationButton{
        @include buttonStyle;
        margin-top: 2px;
        margin-bottom: 10px;
        z-index: 10;   
    }
        

}




// Tablet Styles
@media screen and (min-width: 620px){
    /* background-color: green; */
    .locationContainer{
        @include tabletViewContainer;
        .content{
            grid-column: 3 /9;
            grid-row: 3 / 11;
            z-index: 3;
        }

        .blurredDiv{
            grid-column: 3 / 9;
            grid-row: 3 / 11;  
            background: rgba(33, 63, 54, 1%);
            z-index: 2;
            border-radius: 5%;
            backdrop-filter: blur(20px);
            box-shadow: 8px 8px 12px #1c271e;
            width: 100%;
        }
    }
}

// Desktop Styles
@media screen and (min-width: 1200px){
    .locationContainer{
        @include desktopViewContainer;

        .blurredDiv{
            grid-column: 5 / 9;
            grid-row: 3 / 11;
            width: 530px;
            height: 460px;
        }

        .content{
            grid-column: 5 / 9;
            z-index: 10000;  

            h3{
                font-size: 3rem;
                color: $green;
            }

            p{
                font-size: 1.3rem;
            }
        }

        .decoration{
            height: 30%;
            width: 100%;    
            align-self: flex-end;
        }

        .leafDesign{
            width: 70%;
            height: 90%;
            margin-left: 16%;
        }

    }
}