$white: #FFF;
$greenDark: rgb(200, 218, 82);
$grey: rgb(229, 238, 228);
$green: rgb(210, 255, 203);
$font-weights: (
    "normal": 400,
    "medium" : 500,
    "bold" : 600
);

@mixin buttonStyle{
    background: linear-gradient(to right, #6cab25, #185903);
    width: 207px;
    height: 49px;
    padding: 15px;
    border-radius: 50px;
    border: none;
    color: #FFF;
    font-size: 18px;
    text-align: center;
    font-weight: 600;
    cursor: pointer;
    transition-duration: all 0.2s;
    z-index: 8888;

    
    @media screen and (min-width: 1200px){
        &:hover{
            background: linear-gradient(to right, rgb(85, 134, 29), rgb(17, 61, 2));
        }
    }

};

@mixin navbarDisplayFlex{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #333;
}


@mixin mobileViewContainer{  
    width: 100%;
    display: grid;
    background-size: auto;
    background-position: center;
    background-repeat: no-repeat;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(12, 1fr);
};

@mixin tabletViewContainer{
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(12, 1fr);
};

@mixin desktopViewContainer{
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(12, 1fr);
}


