// Mobile Styles
.headerContainer{
    @include mobileViewContainer;
    height: 100vh;
}

.content{
    grid-column: 2 / 8;
    grid-row: 4;
    gap: 2fr;
    text-align: center; // maybe turn into a variable
    color: $white;

    h1{
        font-size: 3.5rem;
        letter-spacing: 1px;
        color: $grey;
        padding-bottom: 10px;
    }

    &:nth-child(2){
        font-size: 1.8rem;
        font-weight: map-get($font-weights, normal);
        padding-top: 30px;
        color: $green;
    }
    
}

.plants{
    grid-column: 3 / 7;
    grid-row: 7 / 10;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-around;

    img{
        width: 70px;
        height: 70px;
    }
}


// Tablet Styles
@media screen and (min-width: 620px){
    .headerContainer{
        @include tabletViewContainer;
    }

    .content{
        grid-column: 2 / 10;
        grid-row: 5 / 6;
    }

    .plants{
        grid-column: 3 / 9;
        grid-row: 8 / 9 ;
        flex-direction: row;
    }
}

// Desktop Styles
@media screen and (min-width: 1200px){
    .headerContainer{
        @include desktopViewContainer;
    }

    .content{
        grid-column: 4 / 10;
        grid-row: 7;

        h1{
            font-size: 3.8rem;
            font-weight: map-get($font-weights, bold);
        }

        p{
            font-size: 1.8rem;
            font-weight: map-get($font-weights, medium);
        }
    }

    .plants{
        grid-column: 5 / 9;
        grid-row: 11 ;
        justify-content: space-between;

        & > *:hover{
            transform: translateY(-10px);
            transition: all .4s ease-in-out;
        }
    }

}
