// Mobile Styles
.footerContainer{
    @include mobileViewContainer;
    height: 30%;
    padding: 20px;
    background-color: #222;
    display: flex;
    justify-content: center;
    align-items: center;
    
    a{
        color: $white;
        margin: 20px;
        font-size: 1.2rem;
        text-decoration: none;
    }
}

.wrapper{
    flex-direction: column;
    grid-column: 1 / 9;
    text-align: center;

    .socials{
        margin: 30px;
        color: $white;
    }

    .socials > *{
     
        font-size: 2rem;
    }
   
    small{
        font-size: 1rem;
    }

    & > * {
        margin: 5px;
    }
}