// Mobile Styles
.navbarContainer{
    @include mobileViewContainer;
    height: 60px;
    position: fixed;
    z-index: 10000;
    

    a{
        color: white;
        font-size: 1rem;    
        font-weight: map-get($font-weights, bold);
        letter-spacing: 1px;      

        &:active{
            border-bottom: 3px solid $white;
           
        }
    }

    .toggleBox svg{
        color: rgb(200, 218, 82);
        width: 1.5rem;
        height: 1.5rem;
    }
}

#closed{
    @include navbarDisplayFlex;
     
    nav{
        visibility: hidden;
    }

}

#opened{
    @include navbarDisplayFlex;
    
    .nav{
      
        visibility: visible;
    }
    
    ul{
        position: fixed;
        margin-top: 30px;
        height: 250px;
        left: 0;
        background-color: #222;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    ul a{
        margin: 15px;
        font-size: 1.3rem;
    }

}


// Desktop Styles
@media screen and (min-width: 1200px){
    .navbarContainer{
        @include desktopViewContainer;
    }

    #closed{
        background-color: transparent;
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        
        .navigation{
            visibility: visible;
        }
                    
        .navigation a{
            margin: 20px;
            text-decoration: none;
            font-size: 1.1rem;  

            &:hover{
                color: $greenDark;
                transition: all 0.2s;
            }
        }

        .navigation li{
            display: inline-block;
        }
        
        & a:hover{
            transform: translate(50px, 100px);
        }
    }

    .toggleBox svg{
        display: none;
    } 
}



